nav {
  display: flex;
  width: 100%; }
  nav img {
    width: 4rem;
    height: 3rem;
    margin: 1rem 1rem 0 1.2rem; }
  nav ul {
    width: 100%;
    display: flex;
    justify-content: flex-end;
    margin-block-start: 0;
    margin-block-end: 0;
    padding-inline-start: 10px; }
    nav ul .warp-manu {
      display: flex;
      flex-direction: row; }
      nav ul .warp-manu li {
        display: flex;
        padding: 6px 8px; }
        nav ul .warp-manu li h6 {
          color: black;
          margin: 0.5rem 0.5rem;
          padding: 0.5rem 0; }

.warp-cover {
  width: 100%;
  display: flex;
  justify-content: center; }
  .warp-cover .warp-text {
    display: flex;
    flex-direction: column;
    align-items: center;
    position: absolute;
    width: 100%;
    margin: 1rem;
    padding: 3rem; }
    .warp-cover .warp-text button {
      margin: 1.5rem;
      background-color: #ffe8fc;
      border: 1px solid;
      padding: 1rem;
      border-radius: 1rem; }
      .warp-cover .warp-text button .btn {
        font-size: 1.5rem; }
  .warp-cover .cover {
    width: 100%;
    border-radius: 1rem;
    display: flex;
    justify-content: center;
    margin: 1rem;
    flex-direction: row;
    background-image: linear-gradient(to right top, #ffe8fc, #feeafd, #f6e9ff, #f5e9ff, #f1e6ff); }
    .warp-cover .cover img {
      width: 50rem;
      margin-top: 10rem; }

h1 {
  color: black;
  font-size: 4rem !important;
  text-align: center;
  margin-bottom: 0;
  margin-block-start: 0;
  margin-block-end: 0;
  font-family: 'Prompt', sans-serif; }

h6 {
  color: black;
  font-size: 2rem !important;
  text-align: center;
  font-family: 'Prompt', sans-serif;
  margin-bottom: 0; }
